import {
    CHANGE_SEARCH_FIELD,
    REQUEST_ROBOTS_PENDING,
    REQUEST_ROBOTS_SUCCESS,
    REQUEST_ROBOTS_FAILED,
}
    from './constants.js';

// here we are returning an Object so thunkMidddleware can recognize
export const setSearchField = (text) => {
    return ({
        type: CHANGE_SEARCH_FIELD,
        payload: text,
    })
}
// Here we are not returning an object or function at first,
// so we make a change to function, so only chance is to make it an higher order fucntion
export const requestRobots = ()=> (dispatch) => {
    dispatch({
        type: REQUEST_ROBOTS_PENDING
    })
    fetch('https://jsonplaceholder.typicode.com/users')
        .then(response => response.json())
        .then(data => dispatch({
            type: REQUEST_ROBOTS_SUCCESS,
            payload: data
        }))
        .catch(error => dispatch({
            type: REQUEST_ROBOTS_FAILED,
            payload: error
        }))
}